import React from 'react'
import { Container, Row, Col, Image } from 'react-bootstrap';
import video from '../assets/chan-home-2.mp4';
import poster from '../assets/chan-home.jpg';
import christmas from '../assets/christmas.jpg';
import children from '../assets/passion.jpg';
import newyear from '../assets/2022.jpg';


export default function Body() {


  return (
    <div className="home--container">
      <Row>
        <div className="video-container video-home">
          <video className='videoTag' autoPlay loop muted poster={poster}>
            <source src={video} type='video/mp4' />
          </video>

          <div className="home">
            <h1><span>HELLO!🙂 </span>  WELCOME TO CHASKANCHA</h1>
            <div class="text">
              <p>PROVING HIGH QUALITY, PREMIUM SOURCED PRODUCTS</p>

            </div>
          </div>
          <div className="homeImage">
<div>

<img src={newyear} /> 
    <span className="caption">MISSION.</span>
</div>
<div>
<img src={children} />
<span className="caption">PASSION.</span>  

</div>
<div>

  
<img src={christmas} />
    <span className="caption">VISION.</span>

</div>


          </div>

            

          </div>
        


      </Row>
    </div>
    // </Container>
  )
}
