import React from 'react'
import {Row} from 'react-bootstrap';
import video from '../assets/warehouse-video.mp4';
import poster from '../assets/products-poster.jpg';
import image from '../assets/products-1.jpg';
import products from '../assets/globe.jpg';
import ScrollAnimation from 'react-animate-on-scroll';
import product1 from '../assets/product1.jpg';
import product2 from '../assets/product2.jpg';
import product3 from '../assets/product3.jpg';
import product4 from '../assets/product4.jpg';
import product5 from '../assets/product5.jpg';
import product6 from '../assets/product6.jpg';

import toys1 from '../assets/toys-1.jpg';
import toys2 from '../assets/toys-2.jpg';
import toys3 from '../assets/toys-3.jpg';
import toys4 from '../assets/toys-4.jpg';
import toys5 from '../assets/toys-5.jpg';
import toys6 from '../assets/toys-6.jpg';
import toys7 from '../assets/toys-7.jpg';
import toys8 from '../assets/toys-8.jpg';
import toys9 from '../assets/toys-9.jpg';
import toys10 from '../assets/toys-10.jpg';
import toys11 from '../assets/toys-11.jpg';
import toys12 from '../assets/toys-12.jpg';
import toys13 from '../assets/toys-13.jpg';
import toys14 from '../assets/toys-14.jpg';
import toys15 from '../assets/toys-15.jpg';
import toys16 from '../assets/toys-16.jpg';
import toys17 from '../assets/toys-17.jpg';
import toys18 from '../assets/toys-18.jpg';


import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";


export default function Products(props) {
  const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 5,
      slidesToSlide: 5 // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    }
  };

  return (
    // <Container>
    <Row>
 

      <div className="video-container">
        <h1>Our Products</h1>

        <video className='videoTag' autoPlay loop muted poster={poster}>
          <source src={video} type='video/mp4' />

        </video>
      </div>

      <Carousel
        swipeable={true}
  draggable={true}
  showDots={false}
  arrows={false}
  responsive={responsive}
  ssr={true} // means to render carousel on server-side.
  infinite={true}
  autoPlay={props.deviceType !== "mobile" ? true : false}
  autoPlaySpeed={4000}
  keyBoardControl={true}
  // customTransition="all .5"
  transitionDuration={500}
  containerClass="carousel-container"
  removeArrowOnDeviceType={["tablet", "mobile"]}
  deviceType={props.deviceType}
  dotListClass="custom-dot-list-style"
  itemClass="carousel-item-padding-40-px"
      >
        <div>
    <img
      className="carousel-img"
      src={toys1}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys2}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys3}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys4}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys5}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys6}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys7}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys8}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys9}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys10}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys11}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys12}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys13}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys14}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys15}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys16}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys17}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={toys18}
      alt="First slide"
    />
  </div>

   <div>
    <img
      className="carousel-img"
      src={product1}
      alt="First slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={product2}
      alt="Second slide"
    />
  </div> 
  <div>
    <img
      className="carousel-img"
      src={product6}
      alt="Second slide"
    />
  </div> 
  <div>
    <img
      className="carousel-img"
      src={product3}
      alt="Third slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={product4}
      alt="Fourth slide"
    />
  </div>
  <div>
    <img
      className="carousel-img"
      src={product5}
      alt="Fifth slide"
    />
  </div>

</Carousel>
      <div class="info--container">
      <ScrollAnimation animateIn="fadeIn">

        <div className="about">
          <div className="text">
            <p>Chaskancha sells an assortment of high quality products at affordable prices.  A full list of the products we provide will be available soon.</p>
          </div>
          <div className="image">
            <img src={image} />
          </div>
        </div>
        </ScrollAnimation>

        <ScrollAnimation animateIn="fadeIn">

        <div className="about">
        <div className="image">
            <img src={products} />
          </div>
          <div className="text">
            <p>Quality. That's our primary concern.  We want our customers to know that the products they purchase from us are sourced only from the highest quality suppliers.  We will never compromsie if it means our customers receive an inferior product.</p>
          </div>

        </div>
        </ScrollAnimation>
 
      </div>




    </Row>

    // </Container>

  )
}
